body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: sans-serif;
  color: white;
}

main {
  text-align: center;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden; /* Hide overflow to prevent unnecessary scrollbars */
}

.title, .qrapp {
  width: 100vw;
  height: 100vh;
  background: rgb(149,61,246);
  background: linear-gradient(20deg, rgba(149,61,246,1) 0%, rgba(85,53,212,1) 25%, rgba(71,81,234,1) 50%, rgba(53,105,212,1) 75%, rgba(61,171,246,1) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  overflow: auto; /* Allow scrolling within the title div */
}

.title h1,
.title h3,
.qrapp h1,
.qrapp h3 {
  margin: 0;
}

.title h1 {
  font-size: 20vh;
}

.title h3 {
  font-size: 4vh;
}

.content {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  max-width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1.5rem;
  padding-bottom: 50vh;
}

button {
  background-color:rgba(149,61,246,0.4);
	border-radius:28px;
	border:0px;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:28px;
	padding:20px 45px;
	text-decoration:none;
  font-weight: bold;
  margin-top: 1em;
}

.qrapp h1 {
  font-size: 5em;
}

.qrapp h3 {
  font-size: 1.5em;
}

.form {
  background-color: white;
  border-radius: 1em;
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 60%;
  margin-top: 1rem;
}

input {
  border-radius: 0.3em;
  padding: 0.5em;
  border: 1px solid lightgray;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
}

label {
  color: black;
  margin-left: auto;
  margin-right: auto;
}

.form button {
  margin-left: auto;
  margin-right: auto;
  width: 60%;
}

.qrcode {
  margin: 1em;
  padding: 1rem;
  background-color: white;
  border-radius: 1rem;
}

.qrcode h2 {
  color: black;
}